.card-list {
    width: 85vw;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 20px;
  }
@media screen and (max-width:1024px){
    .card-list{
        grid-template-columns: 1fr 1fr;
    }
}  
@media screen and (max-width:600px){
    .card-list{
        grid-template-columns: 1fr;
    }
}
